import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { Camera, Trash2, Loader, Plus } from 'lucide-react';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Textarea } from "@/components/ui/textarea";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import io from 'socket.io-client';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || 'http://localhost:5001';
const WEBSOCKET_URL = process.env.REACT_APP_WEBSOCKET_URL || 'http://localhost:5001';

const App = () => {
  const [images, setImages] = useState([]);
  const [gptResponse, setGptResponse] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [autoAnalyze, setAutoAnalyze] = useState(false);
  const fileInputRef = useRef(null);
  const socketRef = useRef(null);

  useEffect(() => {
    // Initialize socket connection
    socketRef.current = io(WEBSOCKET_URL);

    // Get initial state
    socketRef.current.emit('getState');

    // Listen for state updates
    socketRef.current.on('stateUpdate', (data) => {
      setGptResponse(data.gptResponse);
      setIsLoading(data.isLoading);
      setAutoAnalyze(data.autoAnalyze);
    });

    return () => {
      if (socketRef.current) {
        socketRef.current.disconnect();
      }
    };
  }, []);

  const convertToJpegBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (event) => {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement('canvas');
          canvas.width = img.width;
          canvas.height = img.height;
          const ctx = canvas.getContext('2d');
          ctx.drawImage(img, 0, 0);
          canvas.toBlob((blob) => {
            const reader = new FileReader();
            reader.onloadend = () => {
              const base64data = reader.result.split(',')[1];
              resolve(base64data);
            };
            reader.onerror = reject;
            reader.readAsDataURL(blob);
          }, 'image/jpeg', 0.95);
        };
        img.onerror = reject;
        img.src = event.target.result;
      };
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  };

  const handleCapture = async (event) => {
    const files = Array.from(event.target.files);
    if (files.length > 0) {
      const newImages = await Promise.all(files.map(async file => ({
        file,
        preview: URL.createObjectURL(file),
        base64: await convertToJpegBase64(file)
      })));
      const updatedImages = [...images, ...newImages];
      setImages(updatedImages);

      if (autoAnalyze && updatedImages.length > 0) {
        await handleUpload(updatedImages);
      }
    }
  };

  const handleUpload = async (imagesToUpload = images) => {
    if (imagesToUpload.length === 0) return;

    try {
      setIsLoading(true);
      socketRef.current.emit('updateState', { isLoading: true });
      
      const response = await axios.post(
        `${BACKEND_URL}/process_images`,
        { images: imagesToUpload.map(img => img.base64) },
        { headers: { 'Content-Type': 'application/json' } }
      );

      socketRef.current.emit('updateState', { 
        gptResponse: response.data.finalResponse,
        isLoading: false
      });
    } catch (error) {
      console.error('Error processing images:', error.response ? error.response.data : error.message);
      socketRef.current.emit('updateState', { 
        gptResponse: 'An error occurred while processing the images.',
        isLoading: false
      });
    }
  };

  const handleRemoveOutput = () => {
    socketRef.current.emit('updateState', { 
      gptResponse: '',
      isLoading: false
    });
    setImages([]);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleCaptureClick = () => {
    fileInputRef.current.click();
  };

  const handleRemoveImage = (index) => {
    setImages(prevImages => prevImages.filter((_, i) => i !== index));
  };

  const handleAutoAnalyzeChange = (checked) => {
    setAutoAnalyze(checked);
    socketRef.current.emit('updateState', { autoAnalyze: checked });
  };

  return (
    <div className="flex h-screen w-screen overflow-hidden flex-col sm:flex-row">
      <div className="sm:w-1/4 p-4 overflow-y-auto">
        <Card>
          <CardHeader>
            <CardTitle>Captured Images</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-2 gap-2">
              {images.map((image, index) => (
                <div key={index} className="relative">
                  <img src={image.preview} alt={`Captured ${index + 1}`} className="w-full h-24 object-cover rounded" />
                  <Button
                    variant="destructive"
                    size="sm"
                    className="absolute top-1 right-1"
                    onClick={() => handleRemoveImage(index)}
                  >
                    <Trash2 size={16} />
                  </Button>
                </div>
              ))}
              {images.length < 10 && (
                <Button variant="outline" className="h-24 w-full" onClick={handleCaptureClick}>
                  <Plus size={24} />
                </Button>
              )}
            </div>
            <div>
              <input
                type="file"
                accept="image/*"
                capture="environment"
                onChange={handleCapture}
                className="hidden"
                id="cameraInput"
                ref={fileInputRef}
                multiple
              />
              <Button variant="outline" className="w-full" onClick={handleCaptureClick} disabled={isLoading || images.length >= 10}>
                <Camera className="mr-2 h-4 w-4" />
                Capture Image
              </Button>
            </div>
            <div className="flex items-center space-x-2">
              <Checkbox 
                id="auto-analyze" 
                checked={autoAnalyze} 
                onCheckedChange={handleAutoAnalyzeChange}
              />
              <Label htmlFor="auto-analyze">Auto-analyze on upload</Label>
            </div>
            <Button onClick={() => handleUpload()} className="w-full" disabled={isLoading || images.length === 0}>
              {isLoading ? <Loader className="mr-2 h-4 w-4 animate-spin" /> : null}
              {isLoading ? 'Analyzing...' : 'Analyze Images'}
            </Button>
            <Button onClick={handleRemoveOutput} className="w-full" disabled={isLoading || (!gptResponse && images.length === 0)}>
              <Trash2 className="mr-2 h-4 w-4" /> Clear All
            </Button>
          </CardContent>
        </Card>
      </div>
      
      <div className="sm:w-3/4 p-4">
        <Card className="h-full">
          <CardHeader>
            <CardTitle>GPT-4 Vision Analysis</CardTitle>
          </CardHeader>
          <CardContent className="h-[calc(100%-4rem)]">
            <Textarea
              value={gptResponse}
              readOnly
              className="h-full resize-none"
              placeholder="Image analysis will appear here..."
            />
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default App;